@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;500;600;700&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
  }

  :root {
    --color-bg: #fac73c;
    --color-bg-variant: #fac73ca8;
    --color-primary: #4bd6ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);
  
    --transition: all 400ms ease;
  }

  html {
    scroll-behavior: smooth;
  }

  
/* ::-webkit-scrollbar{
  display: none;
} */

body {
    font-family: 'Roboto Mono', monospace;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
  }

  /* _________GENERAL STYLE______________ */
  
  h1,h2,h3,h4,h5 {
    font-weight: 500;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  .section {
    margin-top: 4rem;
    margin-bottom: 2rem;
  }
  
  .section>h2,
  .section>h5 {
    text-align: center;
    color: var(--color-light);
  }
  
  .section>h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
  }
  
  .text-light {
    color: var(--color-light);
  }
  
  a {
    color: var(--color-primary);
    transition: var(--transition);
  }
  
  a:hover {
    color: var(--color-white)
  }
  
  .btn {
    width: max-content;
    display: inline-block;
    color: #000;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition:var(--transition);
  }
  
  .btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
  }
  
  .btn-primary {
    background: var(--color-primary);
    color: #fff;
  }

  .btn-primary:hover {
    color: #000;
  }
  
  img {
    display: block;
    width: 100%;
    object-fit: cover;
  }
  
  /* ____________MEDIA QUERIES____________ */
  
  /* MEDIUM DEVICE MEDIA QUERIES */
  
  @media screen and (max-width: 1024px) {
  
    section {
      margin-top: 6rem;
    }
    
  }
  
  /* SMALL DEVICE MEDIA QUERIES */
  
  @media screen and (max-width: 600px) {
    section > h2 {
      margin-bottom: 2rem;
    }
  }
  
  